export default {
	data() {
		return {
			isCard: true,
			jingdian:[],
			cityId:'5d132c7597464815c01eafc1'
		}
	},
	created:function () {
		try {
			const value = uni.getStorageSync('loginUser');
			if (value) {
				this.cityId = value.cityId
			}
			console.log('this.cityId',this.cityId);
			uni.request({
				url: this.websiteUrl+'/api/showCityTickets/'+ this.cityId,
				method: 'GET',
				dataType: 'json',
				success: (res) => {
					this.jingdian = res.data.data;
				}
			})
		} catch (e) {
			// error
			console.log('error',e)
		}
	},
	methods: {
		IsCard(e) {
			this.isCard = e.detail.value
		},
		detail(item){
			uni.navigateTo({
				url:"../jingdian_detail/jingdian_detail?id=" + item._id
			})
		},
		seeWeather(){
			uni.navigateTo({
				url:'../weather/weather?cityName=邯郸'
			})
		}
	}
}
